@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Roboto:wght@100;300&display=swap);
body {
}

/*

this, App.js, is the container for the frame object.
its purpose is
  to be a full screen colored background on which to center the frame

*/

html {

	height: 100%; /* allow the flex display to utilize the entire display area */
}	

body {
	
	margin: 0 ; /*  body has default margin of 10px */

	height: 100%; /* allow the flex display to utilize the entire display area */

	/*  about display: flex:
	      parent element has display flex property.
		  justify-content is horizontal
		  align-items is vertical
		  child elements must have margin: auto for flex to work, flex works on margins */  

	display: -webkit-flex;  

	display: flex;
	-webkit-align-items: center;
	        align-items: center;	
	-webkit-justify-content: center;	
	        justify-content: center;

	background-color:#faf9f9;
	
}


p {
	color: #887777;
	font-family: 'Roboto', sans-serif;
	font-weight: 100;
}

#wrapperDiv {
	/* margin: 100px; */

}


#frame {
	
	background-color: #f2f0f0;

	position: relative;  		/* this might be for its child elements; we will see */
	width: 100vw;		 		/* full width */
	max-width: 70em;	 		/* however do not exceed this value */
	margin: auto;		 		/* for flex to work, flex works on margins */
	padding-top: 65%;    		/* this ensures that height scales with width */
}


#titlen {
	position: absolute;
	top: -3em;
	width: 100%;

	text-align: center;
	font-size: 2.5em;
}

#handle1 {

    position: absolute;
}

#handle2 {

    position: absolute;
}

#draggable1 {
    will-change: transform; /* let browser know to optimize this parameter, avoiding janky motion */
}

#draggable2 {
    will-change: transform; /* let browser know to optimize this parameter, avoiding janky motion */
}
#bildet {

	position: absolute;
	width: 100%;
	height: 50%;
	top: 0;

	
	background-repeat: no-repeat;
	background-size: 100% 100%;
}

#canvasPic {
	position: absolute;

	width:100%;
	height: 100%;
}

#bildetTitle {
	position:absolute;
	margin: 0;
	left: 20px;
	top: 10px;
	width: 800px;
	height: 50px;
	font-size: 1.3em;

}


.graph {

	position: absolute;
	width: 100%;
	height: 25%;
	top: 50%;

	overflow: clip; /* for svg not making scroll bars*/

	background-repeat: no-repeat;
	background-size: 100% 100%;
}

#drawingCanvas {

	
	width:100%;
	height: 100%;
}

#graphTitle {
	position:absolute;
	margin: 0;
	left: 20px;
	top: 10px;
	width: 800px;
	height: 50px;
	font-size: 1.3em;

}

#playIndexLine {
	position: absolute;
	left: calc(-50%);
	height: 100%;
	width: 100%;
	stroke-width: 0.5px;
	stroke: #6b6b6b;
	fill: #6b6b6b; 
	opacity: 0.3;
	will-change: transform; /* let browser know to optimize this parameter, avoiding janky motion */
 	visibility: hidden;
 }

line {
	overflow: clip;
}
.animator {

	position: absolute;
    display: -webkit-flex;
    display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;

	width: 100%;
	height: 25%;
	top: 75%;

	background-repeat: no-repeat;
	background-size: 100% 100%;
	background-color: #DDCCCC;
}

#animatorTitle {
	position:absolute;
	margin: 0;
	left: 20px;
	top: 10px;
	width: 800px;
	height: 50px;
	font-size: 1.3em;
}

#playTriangle {
	position: absolute;
	fill:#6b6b6b;
	stroke:#6b6b6b;
	stroke-width: 0;
	width: min(7vw, 4em);
	height: auto;
	will-change: transform; /* let browser know to optimize this parameter, avoiding janky motion */
}

